<template>
    <div>
        <mhead-top titleHead="新建/编辑收货地址" lfIcon="el-icon-arrow-left icon" :lfUrl="backurl" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="m-edit-add">
                <el-form :rules="rules" ref="form" :model="form" label-position="top" label-width="80px" size="mini">
                    <el-form-item class="m-f-h" label="姓名" prop="name">
                        <el-input size="medium" placeholder="姓名" v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item class="m-f-h" label="手机号码" prop="mobile">
                        <el-input size="medium" placeholder="手机号码" v-model.number="form.mobile"></el-input>
                    </el-form-item>
                    <el-form-item class="m-f-h" label="省份、城市、区县" prop="place">
                        <el-input @focus="drawer = true" placeholder="省份、城市、区" size="medium" v-model="form.province+' '+form.city+' '+form.area" ></el-input>
                        <el-drawer
                                class="g-drawer"
                                title="请选择"
                                :visible.sync="drawer"
                                direction="btt">
                            <v-distpicker type="mobile" @selected="onSelected"></v-distpicker>
                        </el-drawer>
                    </el-form-item>
                    <el-form-item class="m-f-h" label="详细地址，如街道，楼盘号等" prop="address_detail">
                        <el-input type="textarea" placeholder="详细地址，如街道，楼盘号等" v-model="form.address_detail" :rows="4"></el-input>
                    </el-form-item>
                    <div class="formBtn">
                        <div class="m-edit-btn" @click="cancel">取消</div>
                        <div class="m-edit-btn safe" @click="submitForm('form')">保存</div>
                    </div>
                </el-form>
            </div>
        </div>
    </div>
</template>


<script>
    import mheadTop from '../../components/mobile/headTop';
    import {addAddress,editUserAddressShow,editUserAddress,} from '../../service/api'
    import VDistpicker from 'v-distpicker'

    let id = 0;
    export default {
        name: 'mOrderAdd',
        data(){
            return{
                add_id:'',
                goodsId:'',
                speId:'',
                number:'',
                type:0,//0:购物车下单，1：产品详细页下单
                backurl:'',//返回上一页
                submitType:0,//0是新建，1是编辑
                form: {
                    add_id:'',
                    place:'',
                    address_detail:'',
                    mobile:'',
                    name: '',
                    phone: '',
                    province:'',
                    city:'',
                    area:'',
                },
                drawer: false,
                selplace:'',
                rules:{
                    place: [
                        { required: true, message: '请选择所在地区'},
                    ],
                    address_detail: [
                        { required: true, type:"string", message: '请填写详细地址',trigger: 'blur'},
                    ],
                    name: [
                        { required: true,type:"string", message: '请填写收货人',trigger: 'blur'},
                    ],
                    mobile:[
                        { required: true, message: '请填写手机号码'},
                        { type: 'number', message: '手机号必须为数字值'}
                    ]
                }
            }
        },
        components:{
            mheadTop,
            VDistpicker
        },

        mounted(){

            this.initData();
        },
        computed:{


        },
        methods:{
            async initData(){

                this.goodsId = this.$route.query.goodsId||'';
                this.speId = this.$route.query.speId||'';
                this.number = this.$route.query.number||0;
                this.type = this.$route.query.type||0;

                this.backurl = '/order/confirm?goodsId='+this.goodsId+'&speId='+this.speId+'&number='+this.number+'&type='+this.type;

                this.add_id = this.$route.query.add_id || '';

                if(this.add_id!=''){
                    this.submitType = 1;
                    var res = await editUserAddressShow(
                        this.add_id
                    );
                    this.form.add_id = res.address_id;
                    this.form.place = res.province +' '+res.city+' '+res.district;
                    this.form.address_detail = res.address_detail;
                    this.form.mobile = parseInt(res.mobile);
                    this.form.name = res.name;
                    this.form.province = res.province;
                    this.form.city = res.city;
                    this.form.district = res.district;
                }
            },
            submitForm(formName) {
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.submitType==0){//0:添加地址；1：编辑修改地址
                            var res = await addAddress(
                                this.form.address_detail,
                                this.form.mobile,
                                this.form.name,
                                this.form.province,
                                this.form.city,
                                this.form.area
                            );

                        }else{
                            var res = await editUserAddress(
                                this.form.add_id,
                                this.form.address_detail,
                                this.form.mobile,
                                this.form.name,
                                this.form.province,
                                this.form.city,
                                this.form.area
                            );
                        }
                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                this.$router.push({ path: '/order/address',query:{goodsId:this.goodsId,speId:this.speId,number:this.number,type:this.type}});
                            }
                        }

                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            onSelected(data) {
                var _this = this;
                _this.form.province = data.province.value;
                _this.form.city = data.city.value;
                _this.form.area = data.area.value;
                _this.form.place = data.province.value+ data.city.value + data.area.value;
                console.log(data);
                _this.drawer = false;

            },
            cancel(){
                this.$router.go(-1);
            }
        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .m-edit-add{
        background-color: #fff;
        padding:px2rem(20);
    }
    .formBtn{
        width:100%;
        height: px2rem(104);
        position: fixed;
        bottom:0;
        left:0;
        background-color:$colorwhite;
        border-top:1px solid #eee;
        z-index: 1000;
        display: flex;
        flex-wrap: nowrap;
    }
    .m-edit-btn{
        width:50%;
        height: px2rem(104);
        line-height: px2rem(104);
        text-align: center;
    }
    .m-edit-btn.safe{
        background-color: $default;
        color:#fff;
    }
    .m-f-h{
        margin-bottom:0;
        font-size: px2rem(32);
    }
    .m-f-h .el-form-item__label{
        padding-bottom: 0;
    }

    /*拉起弹框*/
    .g-drawer .el-drawer{
        min-height: px2rem(800);
    }
    .g-drawer .el-drawer__header{
        padding:px2rem(20);
        margin-bottom: 0;
    }
    .address-container{
        height:px2rem(624);
    }



</style>



